import MasterPage from '../componentes/Master';
import Precio from '../componentes/Precio';
import Noticias from '../componentes/Noticias';
import fetch from 'isomorphic-unfetch';

const Index = (props) => (
     <MasterPage>
          <div className="row">
               <div className="col-12">
                    <h2>Precio del bitcoin</h2>
                    <Precio
                         precio={props.precioBitcoin}
                    />
               </div>
               <div className="col-md-8">
                    <h2>Noticias Bitcoin</h2>
                    <Noticias 
                         noticias={props.noticias}
                    />
               </div>
               <div className="col-md-4">
                    <h2>Pr√≥ximos Eventos Bitcoin</h2>
                    <Eventos
                         eventos={props.eventos} />
               </div>
          </div>
     </MasterPage>
);

Index.getInitialProps = async () => {
     const precio = await fetch('https://api.coinmarketcap.com/v2/ticker/1/');
     const noticias = await fetch('https://newsapi.org/v2/everything?q=bitcoin&from=2019-04-03&sortBy=publishedAt&apiKey=b6befcf773db43efae73f7c59d23215b&language=es');
     const eventos = await fetch('https://www.eventbriteapi.com/v3/events/search/?q=bitcoin&location.address=Buenos+Aires&token=3OS7IYV3H7YNCTBU5GLJ');
     
     const resPrecio = await precio.json();
     const resNoticias = await noticias.json();
     const resEventos = await eventos.json();

     return {
          precioBitcoin: resPrecio.data.quotes.USD,
          noticias: resNoticias.articles,
          eventos: resEventos.events
     }
}

export default Index;